<template>
    <div class="searchWrap">
        <header>
            <router-link to="/msite"><i class="el-icon-arrow-left"></i> </router-link>
            <div class="top_title"><span>搜索</span></div>
        </header>
        <div class="form_search">
            <input type="search" placeholder="请输入商家或美食名称" class="search_input" v-model="input_value" v-on:input="change">
            <input type="submit" class="search_submit" value="提交" @click="getSearch">
        </div>
        <div class="search_content" v-if="Show">
            <h4 class="title">商家</h4>
            <ul>
                <li v-for="li in shoplist" :key="li.id">
                    <div class="content_img">
                        <img :src="imgurl+li.image_path" alt="">
                    </div>
                    <div class="content_msg">
                        <p>{{li.name}}</p>
                        <p>月售{{li.recent_order_num}}单</p >
                        <p>{{li.float_delivery_fee}}元起送 / 距离{{li.distance}}</p >
                    </div>
                </li>
            </ul>
        </div>
        <div class="search_history" v-if="isShow">
            <h4 class="title">搜索历史</h4>
            <ul>
                <li v-for="(s, index) in searchNameArr">
                    <span>{{s}}</span>
                    <i class="el-icon-close" @click="deleteOneHistory(index)"></i>
                </li>
            </ul>
            <p @click="clearHistory">清空历史记录</p>
        </div>
        <div class="footer">
            <router-link to="/msite">
                <div>
                    <i class="el-icon-eleme" id="eleme"></i>
                    <span>外卖</span>
                </div>
            </router-link>
            <router-link to="/search">
                <div>
                    <i class="el-icon-stopwatch" id="stopwatch"></i>
                    <span>搜索</span>
                </div>
            </router-link>
            <router-link to="">
                <div>
                    <i class="el-icon-notebook-2"></i>
                    <span>订单</span>
                </div>
            </router-link>
            <router-link to="/profile">
                <div>
                    <i class="el-icon-s-custom"></i>
                    <span>我的</span>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data(){
            return{
                uName:"",
                input_value:"",
                shoplist:[],
                geohash:"",
                imgurl:"http://elm.cangdu.org/img/",
                searchNameArr:[],
                isShow:true,
                Show:false
            }
        },
        created(){
            this.uName = localStorage.getItem("placeNameHistory");
            let newarr = JSON.parse(localStorage.getItem("placeHistory"));
            let filterArr = newarr.filter(v=>{
                return v.name == this.uName;
            });
            this.geohash = filterArr[0].geohash;
            if(localStorage.searchName != undefined){
                this.searchNameArr = JSON.parse(localStorage.getItem("searchName"));
            }else {
                this.isShow = false
            }
        },
        methods:{
            getSearch(){
                this.Show = true;
                this.isShow = false;
                this.axios.get("/v4/restaurants", {
                    params: {geohash:this.geohash ,keyword:this.input_value}
                }).then(res=>{
                    this.shoplist = res;
                    console.log(this.shoplist)
                }).catch(err=>{});


                if(!localStorage.searchName){
                    let arr = [this.input_value]
                    //当localStorage为空时创建数组，并插入到localStorage中
                    localStorage.setItem("searchName",JSON.stringify(arr));
                }else {
                    //当localStorage不为空时，为避免重复插入进行判断过滤
                    let newarr = JSON.parse(localStorage.getItem("searchName"));
                    let filterArr = newarr.filter(v=>{
                        return v == this.input_value;
                    });
                    if(!filterArr.length){
                        newarr.push(this.input_value);
                        localStorage.setItem("searchName",JSON.stringify(newarr));
                    }
                }
            },
            clearHistory(){
                localStorage.removeItem("searchName");
                this.isShow = false;
            },
            change(){
                if(this.input_value){
                    // this.Show = true
                    this.isShow =false
                }else {
                    if(localStorage.searchName != undefined){
                        this.searchNameArr = JSON.parse(localStorage.getItem("searchName"));
                    }else {
                        this.isShow = false

                    }
                    this.Show = false
                    this.isShow = true
                }
            },
            deleteOneHistory(num){
                const data = JSON.parse(localStorage.getItem('searchName'));
                data.splice(num,1);
                if(data.length == 0){
                    localStorage.removeItem("searchName");
                }else{
                    localStorage.setItem('searchName', JSON.stringify(data));
                }
                if(localStorage.searchName != undefined){
                    this.searchNameArr = JSON.parse(localStorage.getItem("searchName"));
                }else {
                    // localStorage.removeItem("searchName");
                    this.isShow = false
                }
            }
        },

    }
</script>

<style scoped>
    .searchWrap{
        overflow: hidden;
    }
    header{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    a{
        text-decoration: none;
    }
    header>a>i{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        color: #fff;
        line-height: 2.2rem;
        margin-left: .4rem;
    }
    .top_title{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .top_title>span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .form_search{
        background-color: #fff;
        padding: .5rem;
        display: flex;
        margin-top: 1.95rem;
    }
    .form_search>input{
        height: 1.5rem;
    }
    .search_input{
        flex: 4;
        border: .025rem solid #e4e4e4;
        font-size: .65rem;
        color: #333;
        border-radius: .125rem;
        background-color: #f2f2f2;
        font-weight: 700;
        padding: 0 .25rem;
    }
    .search_submit{
        flex: 1;
        border: .025rem solid #3190e8;
        margin-left: .2rem;
        font-size: .65rem;
        color: #fff;
        border-radius: .125rem;
        background-color: #3190e8;
        font-weight: 700;
        padding: 0 .25rem;
    }
    .title {
        font-size: .6rem;
        line-height: 2rem;
        text-indent: .5rem;
        font-weight: 700;
        color: #666;
    }
    .search_content ul{
        background-color: #fff;
    }
    .search_content ul li{
        display: flex;
        justify-content: center;
        padding: .5rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .content_img{
        margin-right: .25rem;
        width: 1.7rem;
        height: 1.7rem;
        border: black 1px solid;
    }
    .content_img>img{
        width: 1.7rem;
        height: 1.7rem;
        border: black 1px solid;
    }
    .content_msg{
        font-size: .55rem;
        flex: 1;
        padding-bottom: .25rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .content_msg p{
        line-height: .9rem;
    }
    .search_history ul>li{
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        font: .7rem/2rem Microsoft YaHei;
        padding: 0 .3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .search_history ul>li>span{
        display: inline-block;
        width: 80%;
    }
    .search_history ul>li>i{
        font-size: 1rem;
        font-weight: 900;
        color: rgb(187, 187, 187);
    }
    .search_history>p{
        background-color: #fff;
        color: #3190e8;
        font: .7rem/2rem Microsoft YaHei;
        font-weight: 700;
        text-align: center;
    }
    .footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;
        display: flex;
        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    .footer a{
        display: block;
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .footer a div{
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .footer a div i{
        font-size: .9rem;
        margin-top: .4rem;
        color: #ccc;
    }
    #stopwatch{
        color: #3190e8;
    }
    .footer a div span{
        font-size: .45rem;
        color: #666;
    }
</style>